import React from 'react'
import {Link} from "react-router-dom"

const Footer = () => {
    return(
        <footer>
            <ul>
                <li>
                    <Link to="/Contact">Nous contacter</Link>
                </li>
                <li>
                    <Link to="/mentions-légales">Mentions légales</Link>
                </li>
            </ul>
        </footer>
    )
}
export default Footer