import React,{useState, useEffect} from "react"
import {getPostsResearchTitle, getPostsResearchDescription, getAccess, getPostsResearchTitleCheminots, getPostsResearchDescriptionCheminots} from "../API/index"
import ArticleList from './ArticleList';
const Search = () => {

    const [postsByTitle, setPostsByTitle] = useState([])
    const [postsByDescription, setPostsByDescription] = useState([])
    const [search, setSearch] = useState("")

    useEffect(() => {
        const searchPostsByTitle = async() => {
            let res
            if(getAccess(true)){
                res = await getPostsResearchTitleCheminots(search)
            }else{
                res = await getPostsResearchTitle(search)
            }
            setPostsByTitle(res.data)
        }
        const searchPostsByDescription = async() => {
            let res
            if(getAccess(true)){
                res = await getPostsResearchDescriptionCheminots(search)
            }else{
                res = await getPostsResearchDescription(search)
            }
            setPostsByDescription(res.data)
        }
        searchPostsByTitle()
        searchPostsByDescription()
    },[search])

    return(
        <>
        <div className="title_block">
            <input type="text" autoFocus onChange={(e) => setSearch(e.target.value)} value={search} placeholder="Rechercher ici.."/>
        </div>
        {
            postsByTitle.map(({titre, description, rubrique, gallerie, id}) => {
                return (
                    <ArticleList 
                        titre={titre} 
                        description={description} 
                        rubrique={rubrique} 
                        id={id}
                        gallerie={gallerie}
                        link={`/${rubrique.nom}/${id}`}
                    />
                )
            })
        }
        {
            postsByDescription.map(({titre, description, rubrique, id, gallerie}) => {
                const isExisting = postsByTitle.some((post) => post.id === id)
                if( !isExisting ){
                    return (
                        <ArticleList 
                            titre={titre} 
                            description={description} 
                            rubrique={rubrique} 
                            id={id}
                            gallerie={gallerie}
                            link={`/${rubrique.nom}/${id}`}
                        />
                    )
                }else {
                    return false
                }
            })
        }
        </>
    )

}

export default Search