import React,{useState, useEffect} from 'react'
import {getAllCategories} from "../API/index"
import {Link} from "react-router-dom"
import $ from "jquery"
const NavBar = () => {

    const [categories, setCategories] = useState([])
    const [isNavDisplay, setIsNavDisplay] = useState(true)

    const getCategories = async() => {
        const res = await getAllCategories()
        setCategories(res.data)
    }

    useEffect(() => {
        getCategories()
    },[])

    const toggleNav = () => {
        $('#App').toggleClass('translated',isNavDisplay)
        $('#App').toggleClass('not-translated',!isNavDisplay)
        setIsNavDisplay(!isNavDisplay)
    }
    const displayMenu = () => {
        $('#App').toggleClass('translated',false)
        $('#App').toggleClass('not-translated',true)
        setIsNavDisplay(true)
    }

    const toggleMenu = (bool, index) => {
        $(index).toggleClass('displayed',bool)
        $(index).toggleClass('not-displayed',!bool)
    }

    $('a').on('click', () => displayMenu())

    return(
        <>
        <nav id="mobile">
            <div>
                <button onClick={toggleNav}><img src="https://cdn4.iconfinder.com/data/icons/wirecons-free-vector-icons/32/menu-alt-512.png"></img></button>
                <Link to="/"><img className="logo" src="logo-vers-de-ter.png" alt='logo'></img></Link>
                <Link to={`/Rechercher`}><img src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/0d/OOjs_UI_icon_search.svg/600px-OOjs_UI_icon_search.svg.png"></img></Link>
            </div>
            <div className="navigation">
                <div>
                    <h1>LES VERTS DE TER</h1>
                    <Link to={`/login`}><img src="https://upload.wikimedia.org/wikipedia/commons/7/70/User_icon_BLACK-01.png"></img></Link>
                    <button onClick={toggleNav}>
                        <img src="https://cdn4.iconfinder.com/data/icons/flat-black/512/next.png"></img>
                    </button>
                </div>
                <ul>
                    {
                        categories.map(({nom, rubriques}) => {
                            return (
                                <li key={nom}>
                                    {nom}
                                    <div>
                                        <ul>
                                            {
                                                rubriques.map(({nom}) => {
                                                    return (
                                                        <li>
                                                            <Link to={`/${nom}`}>{nom}</Link>
                                                        </li>
                                                    )
                                                })
                                            }
                                        </ul>
                                    </div>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        </nav>
        <nav id="desktop">
            <div>
                <div>
                    <Link to="/">
                        <img className="logo" src="logo-vers-de-ter.png" alt='logo'></img>
                        <h1>LES VERTS DE TER</h1>
                    </Link>
                    
                </div>
                <div>
                    <Link to={`/Rechercher`}><img src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/0d/OOjs_UI_icon_search.svg/600px-OOjs_UI_icon_search.svg.png"></img>RECHERCHER</Link>
                    <Link to={`/login`}><img src="https://upload.wikimedia.org/wikipedia/commons/7/70/User_icon_BLACK-01.png"></img>MON COMPTE</Link>
                </div>
            </div>
            <ul>
                    {
                        categories.map(({nom, rubriques}, index) => {
                            return (
                                <li key={nom} onMouseOver={() => toggleMenu(true, `#${index}`)} onMouseOut={() => toggleMenu(false, `#${index}`)}>
                                    {nom}
                                    <div>
                                        <ul id={index} className="not-displayed">
                                            {
                                                rubriques.map(({nom}) => {
                                                    return (
                                                        <li>
                                                            <Link to={`/${nom}`}>{nom}</Link>
                                                        </li>
                                                    )
                                                })
                                            }
                                        </ul>
                                    </div>
                                </li>
                            )
                        })
                    }
            </ul>
        </nav>
        </>
    )
}

export default NavBar