import React,{useState, useEffect} from "react"
import {useParams, useRouteMatch, Redirect, useLocation} from "react-router-dom";
import {getPostsByRubrique, getAccess} from "../API/index"
import ArticleList from "./ArticleList"

const Rubrique = () => {


    let {nom} = useParams()
    let {url} = useRouteMatch();
    const [posts, setPosts] = useState([])
    let location = useLocation();
    

    useEffect(() => {
        const getPosts = async() => {
            const res = await getPostsByRubrique(nom)
            setPosts(res.data)
        }
        getPosts()
        
    },[nom])

    if(posts){
        if(getAccess(posts.some(p => p.rubrique.Cheminots)) === false){
            return <Redirect to={{pathname: '/login', state: {from: location}}}/>
        }
    }

    return (
        <>
        <div className="title_block">
            <h2>{nom}</h2>
        </div>
        {
            posts.map(({titre, description, rubrique, id, gallerie}) => {
                return (
                    <ArticleList 
                        titre={titre} 
                        description={description} 
                        rubrique={rubrique} 
                        id={id}
                        key={id}
                        gallerie={gallerie}
                        link={`${url}/${id}`}
                    />
                )
            })
        }
        </>
    )
}

export default Rubrique