import React from 'react'
import {Link} from 'react-router-dom';
import ReactMarkdown from "react-markdown"
import burl from "../env/Config"

const ArticleList = ({titre, description, rubrique, id, link, gallerie}) => {
    return(
        
        <div className="article_list" key={id}>
            <Link to={link}>
            <img src={gallerie.length !== 0 && `${burl}${gallerie[0].url}`} alt={titre} />
            <h1>{titre}</h1>
            <div className="description">
                <ReactMarkdown
                    source={truncateString(description, 100)}
                    escapeHtml={false}
                />
            </div>
            <div>
                <span className="rubrique_name">
                    <img src="https://img2.pngio.com/green-tag-icon-free-green-price-tag-icons-green-tag-png-256_256.png" />
                    {rubrique.nom}
                </span>
            </div>
            </Link>
        </div>    
    )
}


function truncateString(str, num) {
    // If the length of str is less than or equal to num
    // just return str--don't truncate it.
    if (str.length <= num) {
      return str
    }
    // Return str truncated with '...' concatenated to the end of str.
    return str.slice(0, num) + '...'
  }

export default ArticleList