import axios from "axios"
import burl from "../env/Config"

//headers
let headers = {
    "Content-Type": "application/json"
};

export function getLastPosts() {
    return axios.get(`${burl}/posts?_sort=created_at:DESC&_limit=10&rubrique.Cheminots=false`)
}
export function getLastPostsCheminots() {
    return axios.get(`${burl}/posts?_sort=created_at:DESC&_limit=10`)
}

export function getPostById(id) {
    return axios.get(`${burl}/posts/${id}`)
}

export function getPostsByRubrique(rubrique) {
    return axios.get(`${burl}/posts?rubrique.nom=${rubrique}&_sort=created_at:DESC`)
}

export function getPostsResearchTitle(search) {
    return axios.get(`${burl}/posts?titre_contains=${search}&rubrique.Cheminots=false`)
}

export function getPostsResearchDescription(search) {
    return axios.get(`${burl}/posts?description_contains=${search}&rubrique.Cheminots=false`)
}

export function getPostsResearchTitleCheminots(search) {
    return axios.get(`${burl}/posts?titre_contains=${search}`)
}

export function getPostsResearchDescriptionCheminots(search) {
    return axios.get(`${burl}/posts?description_contains=${search}`)
}


export function getAllCategories() {
    return axios.get(`${burl}/categories?_sort=Position:ASC`)
}

export function getAccess(Cheminots) {
    if(Cheminots){
        return verifyCode()
    }else{
        return true
    }
}

export function verifyCode() {
    let code = localStorage.getItem('SNCF');
    if(code){
        const isLength = code.length === 7
        let isLetter = false
        if(code[6]) isLetter = code[6].match(/[a-z]/i);
        const part = code.slice(0,-1)
        return isLength && isLetter && Number.isInteger(parseInt(part))
    }else{
        return false
    }
    
}

export function sendEmail(email) {
    return axios.post(`${burl}/email`,email,{headers})
}